import React, { Component } from 'react'
import { inject, observer } from 'mobx-react'
import { Card, Layout } from 'antd'

@inject('store')
@observer
export default class Member extends Component {
	constructor(props) {
		super(props)
		this.store = this.props.store
	}

	render() {
		const store = this.store
		return (
				<Layout>
					<h1>Members' area</h1>
			</Layout>
		)
	}
}
